function getInMap(value, map) {
    if (map.has(value)) {
        return map.get(value)
    }
    return value
}

export function flag(value) {
    if (value > 0) {
        return '是'
    }
    return ''
}

const genderTypeMap = new Map([[0, '男'], [1, '女'], [2, '妖'], [3, '非男'], [-1, '']])
export function genderType(value) {
    return getInMap(value, genderTypeMap);
}

const familyTypeMap = new Map([[1, '慕容世家'], [2, '大理段氏'], [3, '黄金家族'], [4, '天波杨府'], [6, '爱新觉罗'], [7, '凤阳朱氏'], [0, ''], [-1, '']])
export function familyType(value) {
    return getInMap(value, familyTypeMap);
}

const partyTypeMap = new Map([[1, '峨眉派'], [2, '武当派'], [3, '逍遥派'], [4, '丐帮'], [5, '桃花岛'], [6, '日月教'], [7, '华山派'],
    [8, '明教'], [9, '古墓派'], [0, ''], [-1, '']])
export function partyType(value) {
    return getInMap(value, partyTypeMap);
}

const equipPosMap = new Map([[-1, ''], [0, '武器'], [1, '防具'], [2, '骑宠']])
export function equipPos(value) {
    return getInMap(value, equipPosMap);
}

const mpKindMap = new Map([[0, '阴'], [1, '阳'], [2, '调和']])
export function reqMpKind(value) {
    if (value == -1) return ''
    if (value == 2) return ''
    return getInMap(value, mpKindMap);
}

export function changeMpKind(value) {
    if (value == 2) return '调和'
    if (value == 0 || value == -1) return ''
    return value
}

const debuffTypeMap = new Map([[1, '内伤'], [2, '虚弱'], [3, '盲目'], [4, '外伤'], [5, '剧毒'], [6, '厌食'], [7, '定身'],
    [8, '点穴'], [9, '封气'], [10, '冰封'], [11, '封招'], [100, '随机']])
export function debuffType(value) {
    if (value == 0) return ''
    if (value == 100) return '解除全部'
    if (value < 0) return '造成' + getInMap(-value, debuffTypeMap);
    else return '解除' + getInMap(value, debuffTypeMap);
}

