<template>
  <div>
    <DataPage
        :tabs="tabs"
        :headersSource="headers"
        :itemsSource="items"
        :mapper="convertValue"
        :header-labels="headerLabels"
        @tabChange="selectTab"
    />
  </div>
</template>

<script>
import DataPage from "@/components/DataPage";
import ItemSource from "@/assets/data/item.json";
import {headers, convertValue, tabs, headerLabels} from "@/config/item-config";


export default {
  name: "ItemDataView",
  data() {
    return {
      selectType: 'BOOK',
      tabs,
      headerLabels,
      convertValue,
    };
  },
  methods: {
    selectTab(value) {
      this.selectType = value;
    },
  },
  computed: {
    headers() {
      return headers
    },
    items() {
      return Object.values(ItemSource)
    }
  },
  components: {
    DataPage,
  }
}
</script>

