<template>
  <v-container fluid grid-list-md style="width: 95%; min-width: 95%; overflow: auto" >
    <v-layout style="width: max-content; min-width: 100%; overflow: auto">
      <v-flex>
        <v-tabs
            center-active
            @change="tabChange"
        >
          <v-tab v-for="tab in tabs" :key="tab.value">
            {{ tab.title }}
          </v-tab>
        </v-tabs>
        <v-expansion-panels flat accordion v-model="option">
          <v-expansion-panel>
            <v-expansion-panel-header>
              选项
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <div v-for="filter in selectedTab.dataFilters" :key="filter.label">
                <v-card flat>
                  <v-card-title class="text-subtitle-2">
                    {{ filter.label }}
                  </v-card-title>
                  <v-card-text>
                    <v-chip-group
                        column
                        mandatory
                        active-class="primary--text text--accent-4"
                        v-model="filter.selected"
                    >
                      <v-chip outlined v-for="filterItem in filter.value" :key="filterItem" :value="filterItem" >
                        {{ filterItem }}
                      </v-chip>
                    </v-chip-group>
                  </v-card-text>
                </v-card>
              </div>
              <v-card flat>
                <v-card-title class="text-subtitle-2">
                  表头筛选
                </v-card-title>
                <v-card-text>
                  <v-chip-group
                      column
                      multiple
                      active-class="primary--text text--accent-4"
                      v-model="selectedLabels"
                  >
                    <v-chip outlined v-for="headerLabel in headerLabels" :key="headerLabel.value" >
                      {{ headerLabel.text }}
                    </v-chip>
                  </v-chip-group>
                </v-card-text>
              </v-card>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>

        <v-text-field
            class="mb-2"
            style="max-width: 80vw"
            v-model="search"
            append-icon="mdi-magnify"
            label="搜索"
            single-line
            hide-details
            clearable
        ></v-text-field>
        <div style="max-width: 80vw; overflow: scroll">
          <div style="width: max-content">

            <v-data-table
                dense
                multi-sort
                show-group-by
                :footer-props="{ 'items-per-page-options': [10, 20, 50, 100, -1] }"
                :calculate-widths="true"
                :headers="headers"
                :items="items"
                :items-per-page="50"
                :search="search"
                class="elevation-1 table"
            >
              <template v-for="col in searchableCol" v-slot:[`item.${col}`]="{ item }">
                <!--              <v-chip :key="col" v-if="item[`${col}`]" @click="chipClick" label outlined>-->
                <!--                {{ item[`${col}`] }}-->
                <!--              </v-chip>-->
                <a :key="col" v-if="item[`${col}`]" @click="chipClick">{{ item[`${col}`] }}</a>
              </template>
            </v-data-table>
          </div>
        </div>

      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
function dataFilterFunc(filters) {
  return (item) => {
    let result = true
    if (filters) {
      filters.forEach(filter => {
        if (filter.selected) {
          result = result && filter.filterFunc(filter.selected, item)
        }
      })
    }
    return result
  }
}

export default {
  name: "DataPage",
  data() {
    return {
      search: '',
      option: 0,
      selectedGroup: this.tabs.length > 0 ? this.tabs[0].value : '',
      selectedLabels: Array.from( {length: this.headerLabels.length}, (v, i) => i),
    };
  },
  computed: {
    headers() {
      return this.headersSource
          .filter(h => h.group == 'ALL' || h.group.split('|').includes(this.selectedGroup))
          .filter(h => {
            if (h.label) {
              return this.selectedLabels.includes(this.headerLabels.findIndex(l => l.value == h.label))
            } else {
              return true
            }
          })
    },
    items() {
      return this.itemsSource.map(this.mapper).filter(i => i.tabGroup == this.selectedGroup)
          .filter(dataFilterFunc(this.selectedTab.dataFilters))
    },
    selectedTab() {
      return this.tabs.find(t => t.value == this.selectedGroup)
    },
    searchableCol() {
      return this.headers.filter(h => h.searchable).map(h => h.value)
    }
  },
  methods: {
    tabChange(tab) {
      const tabItem = this.tabs[tab]
      this.selectedGroup = tabItem.value
      //this.$emit('tabChange', tabItem.value)
    },
    chipClick(event) {
      this.search = event.target.innerText
    },
  },
  props: {
    tabs: Array,
    headersSource: Array,
    itemsSource:  Array,
    headerLabels: Array,
    mapper: Function,
  },
  emits: ['tabChange']
}
</script>

<style scoped>
.row {
  width: fit-content;
}
</style>