export function convertValue(item) {
    const newItem = {}
    // 增加tabGroup字段
    newItem['tabGroup'] = itemTypeDef[item.type]
    // 设置秘籍级别
    if (item.targetSkill) {
        const skill = searchSkillText(item.targetSkill)
        if (!skill) {
            console.log('未找到技能', item.targetSkill)
        }
        switch(skill.type) {
            case 0: newItem['level'] = item.attr_ts; break;
            case 1: newItem['level'] = item.attr_fs; break;
            case 2: newItem['level'] = item.attr_ss; break;
            case 3: newItem['level'] = item.attr_ws; break;
            case 4: newItem['level'] = item.attr_rs; break;
        }
    }

    Object.entries(item).forEach(([key, value]) => {
        if (key == "name") {
            //newItem[key] = "<a href='#/data/item/" + item.id + "'>" + value + "</a>"
            newItem[key] = itemFieldDef[key] ? itemFieldDef[key](value) : value
        } else {
            newItem[key] = itemFieldDef[key] ? itemFieldDef[key](value) : value
        }
    })
    return newItem
}