import ItemSource from '@/assets/data/item.json';
import * as cf from "@/utils/TypeValueConverter";

function header(group, text, value, ext) {
    return {
        group: group,
        text: text,
        value: value,
        groupable: false,
        ...ext
    }
}

const itemFieldDef = {
    'type': cf.itemType,
    'req_gender': cf.genderType,
    'req_family': cf.familyType,
    'req_party': cf.partyType,
    'req_mpKind': cf.reqMpKind,
    'attr_mpKind': cf.changeMpKind,
    'position': cf.equipPos,
    'debuff_type': cf.debuffType,

    'flag_newbie': cf.flag,
    'flag_forge': cf.flag,
}

function convertValue(item) {
    const newItem = {}
    Object.entries(item).forEach(([key, value]) => {
        newItem[key] = itemFieldDef[key] ? itemFieldDef[key](value) : value
    })
    return newItem
}

