const state = () => ({
    selectType: 'STORY'
})

const getters = {
    headers(state) {
        return headers.filter(h => h.group == 'ALL' || h.group.split('|').includes(state.selectType))
    },
    datas(state) {
        return Object.values(ItemSource)
            .filter(i => i.type == itemTypeDef[state.selectType])
            .map(i => convertValue(i))
    }
}

const actions = {

}

const mutations = {
    selectType(state, type) {
        state.selectType = type
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
}